import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
    {
      path: '/',
      redirect: '/home',
    },
    {
      path: '/login',
      component: ()=>import('../page/login/Login.vue'),
    },
    {
      path: '/home',
      component: ()=>import('../page/home/Home.vue'),
    },
    {
      path: '/caseHome',
      component: ()=>import('../page/caseHome/CaseHome.vue'),
      children:[
        {
          path: 'HomeInner',
          component:()=>import('../page/caseHome/children/HomeInner.vue')
        }
      ],
    },
    {
      path: '/caseBirthday',
      component: ()=>import('../page/caseBirthday/CaseBirthday.vue'),
      children: [
        {
          path: 'CaseInner',
          component: ()=>import('../page/caseBirthday/children/CaseInner.vue'),
        },
        {
          path: 'CaseInner2',
          component: ()=>import('../page/caseBirthday/children/CaseInner2.vue'),
        }
      ],
    },
    {
      path: '/caseDinner',
      component: ()=>import('../page/caseDinner/CaseDinner.vue'),
      children: [
        {
          path: 'CaseInner',
          component: ()=>import('../page/caseDinner/children/CaseInner.vue'),
        },
        {
          path: 'CaseInner2',
          component: ()=>import('../page/caseDinner/children/CaseInner2.vue'),
        },
      ],
    },
    {
      path: '/casePrivate',
      component: ()=>import('../page/casePrivate/CasePrivate.vue'),
      children: [
        {
          path: 'privateInner',
          component: ()=>import('../page/casePrivate/children/privateInner.vue'),
        },
        {
          path: 'privateInner2',
          component: ()=>import('../page/casePrivate/children/privateInner2.vue'),
        },
      ],
    },
    {
      path: '/createactive',
      component: ()=>import('../page/createactive/CreateActive.vue'),
    },
    {
      path: '/case',
      component: ()=>import('../page/case/CaseInner.vue'),
    },
    {
      path: '/mine',
      component: ()=>import('../page/mine/Mine.vue'),
      children: [
        {
          name: "listDetail",
          path: '/mine/children/:bid',
          component: ()=>import('../page/mine/children/listDetail.vue'),
        },
      ],
    },
    {
      path:'/detail',
      component:()=>import('../page/detail/ListDetail.vue'),
    },
  ],
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {

      return { x: 0, y: 0 }
    }
  }
})


