// 引入后端接口

// action去commit 一个mutation,

export default {
  showSideBar({commit}){
    commit('showBar')
  },
  hideSideBar({commit}){
    commit('hideBar')
  },
  // 顶部变色
  rollDistance({commit}){
    commit('rollDis')
  },
  // 应用mutations
  setUser({commit},user){
    commit("userState",user)
  }
}



