import vue from 'vue'
import vuex from 'vuex'
import mutations from './mutations'
import actions from './actions'
import getters from './getters'

vue.use(vuex)

const state = {
  isLoading: false,
  isShow:false,

  isLogin:false,  // 是否登录
  currentUser: null, // 当前用户
  token: "",
  seqnum: "",
  version: "",
  uid: "",
  language: "",
}

export default new vuex.Store({
  state,
  getters,
  actions,
  mutations,
})

//  在index这个文件中，我们会去定义我们需要在vuex中存储的状态初始值。
/*
  mutations.js
    - 最常用mutations.js里面的方法，更改vuex中的store里的state的唯一的方式就是提交mutation；
    - vuex中，每个mutation都有一个字符串的事件类型（mutations-type）和一个回调函数（handler）；
      - 回调函数可接受两个参数，第一个参数为state；第二个参数是mutation的载荷（payload）
*/



