// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import './config/rem'
import store from './store/index.js'

import axios from 'axios'
import {post,fetch,patch,put} from './api/https'
//定义全局变量
Vue.prototype.$axios = axios
Vue.prototype.$post=post;
Vue.prototype.$fetch=fetch;
Vue.prototype.$patch=patch;
Vue.prototype.$put=put;

import { Tab, Tabs } from 'vant';
Vue.use(Tab).use(Tabs);

import { Popup } from 'vant';
Vue.use(Popup);

import { Picker } from 'vant';
Vue.use(Picker);

import { DatetimePicker } from 'vant';
Vue.use(DatetimePicker);

import { RadioGroup, Radio } from 'vant';
Vue.use(RadioGroup);
Vue.use(Radio);

import { Cell, CellGroup } from 'vant';
Vue.use(Cell);
Vue.use(CellGroup);

import { Toast } from 'vant';
Vue.use(Toast);

/*import { Lazyload } from 'vant';
Vue.use(Lazyload, {
  preLoad: 1.3,
  attempt: 1
});*/

// 导入轮播图插件
import VueAwesomeSwiper from 'vue-awesome-swiper';
import 'swiper/dist/css/swiper.css'; // require('swiper/dist/css/swiper.css'); 后端导入方式
// 使用轮播图插件
Vue.use(VueAwesomeSwiper, /* { default global options } */);
// 图片懒加载
/*
import VueLazyload from 'vue-lazyload'
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: 'static/error.gif',
  loading: 'static/loading.gif',
  attempt: 1
});
*/

import VueI18n from 'vue-i18n'
import LangStorage from './common/lang'
Vue.use(VueI18n) // 通过插件的形式挂载

const i18n = new VueI18n({
  locale: LangStorage.getLang('zh'),  // 获取用户习惯或初始值  // 语言标识
  messages: {
    'zh': require('./common/lang/zh'),
    'en': require('./common/lang/en')
  }
})

import './assets/css/my-mint.scss'; //全局修改mint-UI样式

/*import animate from 'animate.css' // 使用animate
Vue.use(animate)*/

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  i18n,
  router,
  store,
  components: { App },
  template: '<App/>'
})



