(function(doc, win) {
    var docEl = doc.documentElement, // 获取根元素html
        resizeEvt = 'orientationchange' in window ? 'orientationchange' : 'resize', // https://blog.csdn.net/yytoo2/article/details/78887869
        recalc = function() {
            var clientWidth = docEl.clientWidth;
            if (!clientWidth) return;
            docEl.style.fontSize = 100 * (clientWidth / 750) + 'px';  // https://www.jianshu.com/p/afbf518cc891
        };
    if (!doc.addEventListener) return;
    win.addEventListener(resizeEvt, recalc, false);
    doc.addEventListener('DOMContentLoaded', recalc, false);
})(document, window);



