/*
  * @method: 只传入state，修改loading状态
  * @param（bool）isLoading: isLoading状态
  *
    - 最常用mutations.js里面的方法，更改vuex中的store里的state的唯一的方式就是提交mutation；
    - vuex中，每个mutation都有一个字符串的事件类型（mutations-type）和一个回调函数（handler）；
      - 回调函数可接受两个参数，第一个参数为state；第二个参数是mutation的载荷（payload）
  */

export default {
  showBar(state){
    state.isShow = true;
  },
  hideBar(state){
    state.isShow=false
  },
  // 更改用户状态信息
  userStatus(state,user){
    if(user){
      state.currentUser = user;
      state.isLogin = true;
    } else if(user == null){
      // 登出时，清空sessionStorage里内容
      sessionStorage.setItem("userName",null);
      sessionStorage.setItem("userToken","");
      state.currentUser = null;
      state.isLogin = false;
      state.token = "";
    }
  }
}





